.page {
    &__content {
      //padding: 80px 300px 0;
      //display: flex;
      justify-content: space-between;
      font-size: 25px;
    }
    &__link {
      display: flex;
      justify-content: space-around;
      // padding-top: 200px;
      //flex-direction: column;
      &-button {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
        text-align: center;
        color: white;
        text-decoration: none;
        font-size: 35px;
      }
      &-img {
        height: 260px;
  
       // border-radius: 11px;
      }
    }
  
    &__logo {
      position: relative;
      &-static, &-motion {
        position: absolute;
        width: auto;
        height: 40%;
      }
      &-motion {
        animation: logo-spin infinite 5s linear;
      }
    }
  }
  
  @keyframes logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  
  .qr-block {
    margin-top: 40px;
    // margin-bottom: 40px;
    display: flex;
    justify-content: center;
  }
  .page_one{
      text-align: end;
      margin-top: 20px;
  }
  .page_two{
      text-align: center;
      
  }
  .page_three{
      text-align:start;
     
  }